@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital@0;1&display=swap');

body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding: 1rem;
}


li{
  padding: 0.5rem;
  margin-bottom: 5px;
  text-align: left;
}
ul{
  list-style:none;
  display:inline-block;
  padding-inline-start: 0px;
  width: 300px;
}

h1 {
  color: green;
  font-style: italic;
}
input{
  border: 0;
  border-bottom: 1px solid #C84B31;
  outline: 0;
  background-color:  #191919;
  color: #ECDBBA;
  font-size: large;
}
.error {
  margin-top: 20px;
  color: red;
  background: #191919;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.succes{
  margin-top: 20px;
  color: green;
  background: #191919;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 10px;
 
}

body{
  background-color: #191919;
  color: #ECDBBA;
}

button {
  color: #C84B31;
  padding: 0.2em 1em;
  font-size: 18px;
  border-radius: 0.5em;
  background:  #191919;
  border: 1px solid #e8e8e8;
  transition: all .3s;
  margin-top: 10px;
  
 }
 .btn-add{
  color:#ECDBBA;
  
 }
 button:hover {
  border: 1px solid white;
 }
 
.container{
  display:grid;
   grid-template-columns: 1fr 1fr;
height: 100vh;

}
 
.contacto{
border: 1px solid #2D4263;
padding: 0.5rem;
}

@media (max-width:900px){
  .container{
     display: inline;
     height: 50vw;
  }
}
